import React from "react";
import styled from "@emotion/styled";
import {Box, Button, IconButton, Paper, PaperProps, Typography} from "@mui/material";
import EditIcon from '@mui/icons-material/Edit';
import CopyAllOutlinedIcon from '@mui/icons-material/CopyAllOutlined';

type TemplateListItemProps = {
    icon: React.ReactNode;
    label: string;
} & PaperProps;

const TemplateListItemPaper = styled(Paper)<PaperProps>`
  background-color: rgb(0 0 0 / 6%);
  display: flex;
  justify-content: space-between;
  padding: 12px;
`

const TemplateListItem = ({icon, label, ...props}: TemplateListItemProps) => {
    return <TemplateListItemPaper elevation={2} {...props}>
        <Box display={"flex"} alignItems={"center"}>
            <Box sx={{mr: 1}} display={"flex"} alignItems={"center"}>
                {icon}
            </Box>
            <Typography variant={"h3"} textTransform={"uppercase"} fontWeight={"bold"}>{label}</Typography>
        </Box>
        <Box display={"flex"} alignItems={"center"}>
            <IconButton sx={{mr: 1}}>
                <EditIcon/>
            </IconButton>
            <Button variant={"contained"} startIcon={<CopyAllOutlinedIcon/>}>
                Create Project
            </Button>
        </Box>
    </TemplateListItemPaper>
}

export default TemplateListItem;