import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import CustmapTheme from "./theming/CustmapTheme";
import {CssBaseline, ThemeProvider} from "@mui/material";

declare module '@mui/material/styles' {
    interface Palette {
        glassPrimary: Palette['primary'];
        glassSecondary: Palette['secondary'];
    }
    interface PaletteOptions {
        glassPrimary: PaletteOptions['primary'];
        glassSecondary: PaletteOptions['secondary'];
    }
}

const root = ReactDOM.createRoot(
    document.getElementById('root') as HTMLElement
);
root.render(
    <React.StrictMode>
        <ThemeProvider theme={CustmapTheme}>
            <CssBaseline/>
            <App/>
        </ThemeProvider>
    </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
