import {Box, BoxProps} from "@mui/material";
import React from "react";
import styled from "@emotion/styled";

const CustmapImg = styled.img`
  object-fit: contain;
  height: 100%;
  width: 100%;
`

const CustmapBannerLogo = (props: BoxProps) => {
    return <Box {...props}>
        <CustmapImg
            src={process.env.PUBLIC_URL + "image/custmap logo.png"}
            height={200}
            width={"auto"}
        />
    </Box>
}

export default CustmapBannerLogo;