import {Box, BoxProps} from "@mui/material";
import React from "react";
import styled from "@emotion/styled";
import bannerLogo from "../image/bannerLogo.png"

const CompanyBannerLogoImg = styled.img`
  object-fit: contain;
  height: 100%;
  width: 100%;
`

const CompanyBannerLogo = (props: BoxProps) => {
    return <Box {...props}>
        <CompanyBannerLogoImg
            src={bannerLogo}
            height={200}
            width={"auto"}
        />
    </Box>
}

export default CompanyBannerLogo;