import {Paper, PaperProps, styled} from "@mui/material";

type MainPageGlassPanelProps = {
    color: "primary" | "secondary"
} & PaperProps;

type GlassPaperProps = {
    color: "primary" | "secondary"
}

const GlassPaper = styled(Paper)<PaperProps & GlassPaperProps>`
  background-color: ${({
                         theme,
                         color
                       }) => color === "primary" ? theme.palette.glassPrimary.main : theme.palette.glassSecondary.main};
`

const MainPageGlassPanel = ({color, ...props}: MainPageGlassPanelProps) => {
    return <GlassPaper {...props} color={color}/>
}

export default MainPageGlassPanel;