import {createTheme} from "@mui/material";

const theme = createTheme({
    palette: {
        primary: {
            main: '#3AA8EA',
        },
        secondary: {
            main: '#E9B907',
        },
        background: {
            default: "#F5F5F5"
        },
        glassPrimary: {
            main: "#C4D6EA"
        },
        glassSecondary: {
            main: "#E8E4CB"
        }
    },
    typography: {
        h2: {
            fontSize: "2em"
        },
        h3: {
            fontSize: "1.3em"
        }
    },
    components: {
        MuiButton: {
            styleOverrides: {
                root: {
                    borderRadius: "36px"
                }
            }
        },
        MuiPaper: {
            styleOverrides: {
                root: {
                    borderRadius: "12px"
                }
            }
        }
    }
});

export default theme;